import {
	onBeforeRouteLeave,
	RouteLocationNormalized,
	useRoute,
	// NavigationGuardNext
} from 'vue-router';
import {onMounted, onUnmounted} from 'vue';
import {WindowController} from '../../../core/WindowController';
import {addEditorRoutes, removeEditorRoutes} from '../../../../routes';
import {AppConfig} from '../../../../config/AppConfig';

export function SetupEditorRoutes() {
	onMounted(_addRoutes);
	onUnmounted(_removeRoutes);
	onBeforeRouteLeave(_checkChangesSaved);

	const route = useRoute();

	async function _addRoutes() {
		addEditorRoutes(route);
		await AppConfig.onEditorRoutesCreated();
	}
	function _removeRoutes() {
		removeEditorRoutes();
	}

	function _checkChangesSaved(
		to: RouteLocationNormalized,
		from: RouteLocationNormalized
		// next: NavigationGuardNext
	) {
		// https://router.vuejs.org/guide/advanced/composition-api.html#navigation-guards
		const promptRequired = WindowController.instance().promptRequired();
		// console.log({to, from, promptRequired});
		if (promptRequired) {
			const answer = window.confirm('Do you really want to leave? you have unsaved changes!');
			if (!answer) {
				return false;
			}
		}
		return true;
	}

	return {};
}
