import {NodeContextMenuRegister, NodeContextMenuSetup} from './BaseSetup';

import {BaseNodeClass, BaseNodeType} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/_Base';
import {NodeContext} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/NodeContext';

// hooks
// js
import {animationMixerJsNodeContextMenu} from './js/AnimationMixer';
import {keyframesJsNodeContextMenu} from './js/Keyframes';
// mat
import {rayMarchingBuilderActorNodeContextMenu} from './mat/RaymarchingBuilder';

export class EditorCoreNodeContextMenuHooksController {
	private static _hooks: Map<NodeContext, Map<string, NodeContextMenuSetup<any>>> = new Map();
	private static _registered = false;
	static registerAll() {
		if (this._registered) {
			return;
		}
		this._registered = true;
		this.registerAllContextMenus();
	}
	private static registerAllContextMenus() {
		// js
		this.addRegister(animationMixerJsNodeContextMenu);
		this.addRegister(keyframesJsNodeContextMenu);
		// mat
		this.addRegister(rayMarchingBuilderActorNodeContextMenu);
	}

	private static addRegister<N extends typeof BaseNodeClass, NI extends BaseNodeClass>(
		register: NodeContextMenuRegister<N, NI>
	) {
		const {nodeClass, setupFunc} = register;
		const context = nodeClass.context();
		const type = nodeClass.type();

		let typeMap = this._hooks.get(context);
		if (typeMap == null) {
			typeMap = new Map<string, NodeContextMenuSetup<any>>();
			this._hooks.set(context, typeMap);
		}

		const existingSetup = typeMap.get(type);
		if (existingSetup) {
			console.warn(`existing params label setup for ${context}/${type}`);
			return;
		}

		typeMap.set(type, setupFunc);
	}

	static hook(node: BaseNodeType) {
		return this._hooks.get(node.context())?.get(node.type());
	}
}
