import {KeyframesJsNode} from '../../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/Keyframes';
import {NodeContextMenuRegister, DropDownMenuEntryWithCallback} from '../BaseSetup';
import {StoreController} from '../../../../../store/controllers/StoreController';

export const keyframesJsNodeContextMenu: NodeContextMenuRegister<typeof KeyframesJsNode, KeyframesJsNode> = {
	nodeClass: KeyframesJsNode,
	setupFunc: (node) => {
		const createAnimationActions: DropDownMenuEntryWithCallback = {
			entry: {id: 'open_editor', label: 'Keyframes Editor'},
			callback: () => {
				StoreController.editor.keyframeEditor.open({node: node});
			},
		};

		return [createAnimationActions];
	},
};
